import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { MessageService } from './message.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { HttpParams } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';

import { Task } from './task';
import { Page } from './page';
// import { TASKS } from './mock-tasks';
// import { PAGES } from './mock-pages';

@Injectable({
  providedIn: 'root'
})
export class TaskService {
  private taskUrl = 'api/gettasks/';
  private createURL = 'api/createtask/';
  private updateURL = 'api/updatetask/';
  private removeURL = 'api/removetask/';

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(
    private http: HttpClient,
    private messageService: MessageService
  ) { }

  getTasks(params: any | undefined): Observable<Page> {
    const options = {
      params: null
    };

    if (params.onMain) {
      options.params = new HttpParams()
        .set('page', params.page).set('sortKey', params.sortKey)
        .set('sortWay', params.sortWay).set('onMain', params.onMain);
    } else {
      options.params = new HttpParams().set('page', params.page).set('sortKey', params.sortKey).set('sortWay', params.sortWay);
    }
    console.log('options', options);
    return this.http.get<Page>(this.taskUrl, options);
    // return of(PAGES);
  }

  getTask(id: number): Observable<Task> {
    const url = `${this.taskUrl}/${id}`;
    // this.messageService.add(`TaskService: fetched task id=${id}`);
    // return of(TASKS.find(task => task.id === id));
    return this.http.get<Task>(url);
  }

  addTask(task: any): Observable<Task> {
    console.log('before send', task);
    return this.http.post<Task>(this.createURL, task, this.httpOptions);
  }

  updateTask(task: Task): Observable<Task> {
    console.log('before send', task);
    return this.http.post<Task>(this.updateURL, task, this.httpOptions);
  }

  deleteTask(task: Task | number): Observable<any> {
    const id = typeof task === 'number' ? task : task.id;
    console.log('delete task:', id);
    return this.http.post<Task>(this.removeURL, { id }, this.httpOptions);
  }

  // private log(message: string) {
  //   this.messageService.add(`TaskService: ${message}`);
  // }

  // private handleError<T>(operation = "operation", result?: T) {
  //   return (error: any): Observable<T> => {
  //     // TODO: send the error to remote logging infrastructure
  //     console.error(error); // log to console instead

  //     // TODO: better job of transforming error for user consumption
  //     this.log(`${operation} failed: ${error.message}`);

  //     // Let the app keep running by returning an empty result.
  //     return of(result as T);
  //   };
  // }
}
