import { Component, OnInit } from '@angular/core';

import { Task } from '../task';
import { Page } from '../page';
import { TaskService } from '../task.service';
import { MessageService } from '../message.service';
import { title } from 'process';
// import { TASKS } from '../mock-tasks'; // remove on prod

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.scss']
})
export class TaskComponent implements OnInit {
  tasks: Task[] = [];
  // tasks: Task[] = TASKS; // remove on prod
  // pageData: Page;
  sortKey = 'title';
  sortWay = 'inc';
  page = 1;
  prev = null;
  next = null;

  selectedTask: Task;
  isInserting: boolean;

  constructor(
    private taskService: TaskService,
    // private messageService: MessageService
  ) { }

  ngOnInit(): void {
    this.getTasks();
  }

  getTasks(): void {
    const params = {
      page: this.page,
      sortKey: this.sortKey,
      sortWay: this.sortWay
    };
    this.taskService.getTasks(params).subscribe((data) => {
      console.log(data);
      this.tasks = data.results;
      this.page = data.page;
      this.prev = data.prev;
      this.next = data.next;
    });
  }

  delete(task: Task): void {
    this.tasks = this.tasks.filter(h => h !== task);
    this.taskService.deleteTask(task).subscribe(data => {
      console.log(data);
    });
  }

  add(taskTitle: string, taskDescribe: string, taskUser: string, taskEmail: string, taskConfirmed: any, taskOnMain: any): void {
    // taskUser = taskUser.trim();
    // taskEmail = taskEmail.trim();
    console.log(taskTitle, taskDescribe, taskUser, taskEmail, taskConfirmed, taskOnMain);

    if (!taskTitle || !taskUser || !taskEmail) { return; }

    const newtask = {
      title: taskTitle,
      description: taskDescribe,
      user: taskUser,
      email: taskEmail,
      isCofirmed: taskConfirmed,
      onMain: taskOnMain,
    };
    this.taskService.addTask({ newtask } as any)
      .subscribe(task => {
        this.tasks.push(task);
        console.log(task);
      });
  }

  onSelect(task: Task): void {
    this.selectedTask = task;
    // this.messageService.add(`TaskService: Selected task id=${task.id}`);
  }

  sort(key) {
    if (key === this.sortKey) {
      if (this.sortWay === 'inc') {
        this.sortWay = 'dec';
      } else {
        this.sortWay = 'inc';
      }
    } else {
      this.sortKey = key;
      this.sortWay = 'inc';
    }
    this.page = 1;
    this.getTasks();
  }

  setPage(page) {
    this.page = page;
    this.getTasks();
  }

  onInsert(): void {
    this.isInserting = true;
  }

  onClose(): void {
    this.isInserting = false;
    this.selectedTask = null;
  }

}
